import {ProfileAPI} from "../../api/api";

const ADD_POST = 'ADD_POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE'
const SET_USER_STATUS = 'GET_USER_STATUS'
const DELETE_POST='DELETE_POST'


let initialState = {
    posts: [
        {id: 1, post: 'Hi,how are you', likes: 12},
        {id: 2, post: 'I going to the magazine', likes: 31},
        {id: 3, post: 'Уроки React JS', likes: 4},
        {id: 4, post: 'UI - BLL, выносим данные в сторону', likes: 2},
        {id: 5, post: 'react курсы бесплатно', likes: 9},
        {id: 6, post: 'I going to the magazine', likes: 17},
    ],

    profile: null,
    status: ''
}

export const profileReducer = (state = initialState, action) => {


    switch (action.type) {

        case ADD_POST: {
            return {
                ...state,
                posts: [...state.posts, {id: 7, post: action.newPostText, likes: 0}], //был стейт

            };
        }
        case SET_USER_PROFILE: {
            return {...state, profile: action.profile}

        }
        case SET_USER_STATUS: {
            return {...state, status: action.status}}

        case DELETE_POST: {
            return {...state, posts: state.posts.filter(p => p.id != action.postId)}}


        default:
            return state
    }

};

export const AddPostClick = (newPostText) =>
    ({type: ADD_POST, newPostText})


export const setUsersProfile = (profile) =>
    ({type: SET_USER_PROFILE, profile})

export const setStatus = (status) =>
    ({type: SET_USER_STATUS, status})

    export const deletePost = (postId) =>
    ({type: DELETE_POST, postId})



export const getUsersProfile = (userId) => async (dispatch) => {
      let res = await  ProfileAPI.getProfile(userId)
            dispatch(setUsersProfile(res.data))
        }


export const getStatus = (userId) => async (dispatch) => {
      let res = await  ProfileAPI.getStatus(userId)
            dispatch(setStatus(res.data))
      }

export const updateStatus = (status) => async (dispatch) => {
       let res = await ProfileAPI.updateStatus(status)
            if (res.data.resultCode === 0 ) {
                dispatch(setStatus(status))
            }
        }

