import './App.css';
import Nav from './components/Nav/Nav';
import { Routes, Route} from "react-router-dom";
import React, {Suspense} from "react";
import HeaderContainer from "./components/Header/HeaderContainer";

import {connect} from "react-redux";
import {initializerApp} from "./redux/Reducers/app-reducer";
import Preloader from "./components/common/preloader/Preloader";
import {withSuspense} from "./components/common/withSuspense";



const DialogsContainer = React.lazy(() =>  import('./components/Dialogs/DialogsContainer'))
const ProfileContainer = React.lazy(() =>  import("./components/Profile/ProfileContainer"))
const UsersContainer = React.lazy(() =>  import("./components/Users/UsersConteiner"))
const Login = React.lazy(() =>  import("./components/Login/login.jsx"))


class App extends React.Component {
    componentDidMount() {
        this.props.initializerApp()
    }


    render() {
        if (!this.props.initialized) {
            return <Preloader/>
        }


        return (

                <div className='app-wrapper'>
                    <HeaderContainer/>
                    <Nav/>

                    <div className='app-wrapper-content'>
                        <Routes>
                            <Route path="/dialogs/*" element={  withSuspense(DialogsContainer)}/>
                            < Route path="/users"   element={  withSuspense(UsersContainer)}/>

                            <Route path='/login' element={   withSuspense(Login) }/>
                        </Routes>

                    </div>
                    <Routes>
                        < Route path='/profile' element={   withSuspense(ProfileContainer) }/>
                        <Route path="/profile/:userId*"  element={   withSuspense(ProfileContainer) }/>
                    </Routes>
                </div>


        );
    }
}

const mapStateToProps = (state) => ({
  initialized: state.app.initialized
})



 export default connect(mapStateToProps, {initializerApp})(App);


