import {authAPI} from "../../api/api";

const SET_USERS_DATA = 'auth/SET_USERS_DATA'


let initialState = {
    email: null,
    userId: null,
    login: null,
    isAuth: false


}


export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case SET_USERS_DATA:
            return {
                ...state,
                ...action.payload,

            }
        default:
            return state
    }
}


export const setUserData = (email, userId, login, isAuth) =>
    ({type: SET_USERS_DATA, payload: {email, userId, login, isAuth}})


export const getUserData = () => async (dispatch) => {
    let res = await authAPI.UserData()
    if (res.data.resultCode === 0) {
        let {email, id, login} = res.data.data
        dispatch(setUserData(email, id, login, true))
    }
}

export const login = (email, password, rememberMe, setStatus) => async (dispatch) => {
    let res = await authAPI.login(email, password, rememberMe)
    if (res.data.resultCode === 0) {
        dispatch(getUserData())
    } else {
        setStatus(res.data.messages);
    }
}

export const logout = () => async (dispatch) => {
    let res = await authAPI.logout()
    if (res.data.resultCode === 0) {
        dispatch(setUserData(null, null, null, false))
    }
}