


const SEND_MESSAGE = 'SEND_MESSAGE'

let initialState = {
    dialogs: [
        {id: 1, name: 'Sergio'},
        {id: 2, name: 'Margo'},
        {id: 3, name: 'Edik'},
        {id: 4, name: 'Kevin'},
        {id: 5, name: 'Jhon'},
        {id: 6, name: 'Byhar'}
    ],
    messages: [
        {id: 1, message: 'Hi'},
        {id: 2, message: 'How are you bitch?'},
        {id: 3, message: 'Fuck you'},
        {id: 4, message: 'Love them'},
        {id: 5, message: 'Julia kisa'},
        {id: 6, message: 'idi nahoi!'}
    ],

};


export const  messagesReducer = (state = initialState, action) => {


    switch (action.type) {


          case SEND_MESSAGE: {
              let body = action.newMessageBody
              return  {...state,
                           messages: [...state.messages, {id: 7, message: body}]}

          }
          default:
                return state
      }
 }




export const sendMessageCreator  = (newMessageBody) =>

    ({    type: SEND_MESSAGE, newMessageBody})








