import s from './Nav.module.css'
import {NavLink} from "react-router-dom";


const Link = (props) => {
    return (
        <div className={s.nav}>
            <NavLink to={props.id} className = { navData => navData.isActive ? s.active : s.item } > {props.name} </NavLink>
        </div>
    )
}


const Nav = () => {
 return (
     <nav className={s.nav}>

      <Link id='/profile' name='Profile' />
         <Link id='/dialogs' name='Message' />
         <Link id='/users' name='Users' />
         <Link id='/news' name='News' />
         <Link id='/music' name='Music' />
         <Link id='/settings' name='Settings' />
     </nav>
 );
}


export default Nav;